<template>
  <nav-bar>
    <span slot="title" class="title">员工管理</span>
    <a slot="right-content" class="nav-link btn btn-primary-outline btn-sm pull-xs-right" @click="showForm">
      <i class="fa fa-plus"></i> 添加
    </a>
  </nav-bar>
  <table class="table">
    <thead>
      <tr>
        <th>名称 <i class="fa fa-sort"></i></th>
        <th>账号 <i class="fa fa-sort"></i></th>
        <th>部门</th>
        <th>操作</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="worker in workers">
        <td>{{worker.name}}</td>
        <td>{{worker.login}}</td>
        <td>{{worker.department_text}}</td>
        <td>
          <a v-link="'/worker/'+worker.id" class="btn btn-primary-outline btn-sm">修改</a>
          <button @click="remove(worker)" class="btn btn-primary-outline btn-sm">删除</button>
        </td>
      </tr>
    </tbody>
  </table>
  <new-object :show.sync="showModal">
    <span slot="header">创建新员工</span>
    <div class="content-form" slot="body">
      <form @submit="create" action="/cpanel/workers/2" accept-charset="UTF-8" method="post">

        <div class="form-group row">
          <label for="inputEmail3" class="col-sm-2 form-control-label">名称</label>
          <div class="col-sm-10">
            <input class="form-control" placeholder="名称" type="text" v-model="newWorker.name" />
          </div>
        </div>
        <div class="form-group row">
          <label for="inputEmail3" class="col-sm-2 form-control-label">部门</label>
          <div class="col-sm-10">
            <select prompt="请选择" class="c-select" v-model="newWorker.department">
              <option value="waiter">前台</option>
              <option selected="selected" value="kitchen">厨房</option>
              <option value="counter">总台</option>
            </select>
          </div>
        </div>
        <div class="form-group row">
          <label for="inputEmail3" class="col-sm-2 form-control-label">账号</label>
          <div class="col-sm-10">
            <input class="form-control" placeholder="员工账号" type="text" v-model="newWorker.login" />
          </div>
        </div>
        <div class="form-group row">
          <label for="inputPassword3" class="col-sm-2 form-control-label">密码</label>
          <div class="col-sm-10">
            <input class="form-control" placeholder="输入密码" type="password" v-model="newWorker.password" />
          </div>
        </div>
        <div class="form-group row">
          <label for="inputPassword3" class="col-sm-2 form-control-label">确认</label>
          <div class="col-sm-10">
            <input class="form-control" placeholder="再输入一次密码" type="password" v-model="newWorker.password_confirmation" />
          </div>
        </div>
        <div class="form-group row">
          <label for="inputEmail3" class="col-sm-2 form-control-label"></label>
          <div class="col-sm-10">
            <input type="submit" name="commit" value="保 存" class="btn btn-primary" />
          </div>
        </div>
      </form>
    </div>
  </new-object>
</template>
<script>
  import { showAlert, showLoading, hideLoading } from '../vuex/actions'

  export default {
    vuex: {
      getters: {
        isLogged: state => state.merchant.isLogged,
        access_token: state => state.merchant.merchant.access_token
      },
      actions: {
        showAlert,
        showLoading,
        hideLoading
      }
    },
    data () {
      return {
        showModal: false,
        workers: [],
        newWorker: {
          name: '',
          login: '',
          department: '',
          password: '',
          password_confirmation: ''
        }
      }
    },
    route: {
      data (transition) {
        this.showLoading()
        this.$http.get('http://jiancan.me/api/m1/shops/workers.json', { access_token: this.access_token }).then(function (response) {
          transition.next({
            workers: response.data
          })
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    methods: {
      showForm () {
        this.$set('showModal', true)
      },
      create () {
        this.showLoading()
        this.$http.post('http://jiancan.me/api/m1/workers.json', { access_token: this.access_token, worker: this.newWorker }).then(function (response) {
          this.workers.push(response.data)
          this.newWorker = { name: '', login: '', department: '', password: '', password_confirmation: '' }
          this.showModal = false
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      },
      remove (worker) {
        this.showLoading()
        this.workers.$remove(worker)
        this.$http.delete('http://jiancan.me/api/m1/workers/one.json', { access_token: this.access_token, worker_id: worker.id }).then(function (response) {
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    components: {
      'nav-bar': require('../components/nav-bar.vue'),
      'new-object': require('../components/modal-mask/new-object.vue')
    }
  }
</script>



